.New_Element {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
.New_All Button {
  background-color: #0e8388;
  margin: 10px;
}
.New_All {
  width: 80%;
  height: 95%;
  background-color: #ffffff;
  border-radius: 20px;
  display: flex;
  align-items: center;
  flex-direction: column;
  box-sizing: content-box;
}
.New_All i {
  cursor: pointer;
}
.New_Element_parent {
  display: flex;
  justify-content: space-around;
}
.tablos {
  width: 750px;
  display: flex;
  flex-direction: column;

  text-align: center;
}
.tablos table {
  height: 100%;
  width: 100%;
}
.tablosAF {
  width: 100%;
  height: 70%;
  overflow-y: auto;
}

.tablos td {
  vertical-align: middle;
}
.tablos td Button {
  width: 60%;
  text-transform: capitalize;
}
.New_All h1 {
  letter-spacing: 0.125em;
  line-height: 220.9%;
}
.New_All h2 {
  letter-spacing: 0.125em;
  line-height: 220.9%;
  font-weight: bold;
}
.New_Element form {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.New_Element div {
  width: 308px;
  text-align: center;
}
.New_Element div input {
  border-radius: 7px;
  height: 35px;
}
.New_Element div select {
  text-align: center;
  width: 118px;
  border-radius: 7px;
  border: 2px solid #000000;

  height: 35px;
}
.row {
  justify-content: center;
}
.tablos input {
  width: 100%;
  height: 100%;
  border: none;
  text-align: center;
}
.tablos thead tr {
  text-align: center;
}
.tablos input::-webkit-outer-spin-button,
.tablos input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.New_All {
}
@media only screen and (max-width: 900px) {
  .New_Element div {
    width: 50%;
  }
  .New_All Button {
    margin-top: 5px;
  }
  .tablos {
    width: 70%;
    margin-right: 5px;
  }
  .tablos table {
    width: 100px;
  }
  .tablosAF {
    width: 100%;
  }
  .New_Element button {
    font-size: 10px;
    padding: 5px;
    border-radius: 3px;
  }
}
@media (max-width: 600px) {
  .New_All {
    height: 110%;
  }
}
